<template>
  <!-- 水庫頁面 -->
  <div class="waterpool">
    <!-- 標題 -->
    <div class="row text-center" style="margin: 0 15px 0 15px">
      <div class="col-12 col-xl-12 text-center">
        <span
          class="badge badge-pill badge-info animate__animated animate__fadeInUp title"
          style="animation-duration: 1s; animation-iteration-count: 1"
          >水庫</span
        >
      </div>
    </div>
    <!-- 水庫最新資訊 -->
    <div class="childtitle one">高雄水庫最新水量資訊</div>
    <el-row>
      <el-col :xs="24" :span="12">
        <!-- 圖標說明 -->
        <el-card class="iconExplainCard">
          <el-row type="flex" align="middle">
            <el-col :xs="6" :span="7" class="iconExplainCard_title">
              圖標
              <br />說明
            </el-col>
            <el-col :xs="18" :span="17">
              <div
                v-for="(item, index) in iconimg"
                :key="item"
                class="iconExplain"
              >
                <img :src="item" class="iconExplain_img" />
                <span>{{ iconExplain[index] }}</span>
              </div>
            </el-col>
          </el-row>
        </el-card>
        <!-- 地圖 -->
        <l-map
          class="map"
          :zoom="zoom"
          :options="options"
          :center="center"
          v-if="damData.length !== 0"
        >
          <!-- 載入地圖資料 -->
          <l-tile-layer :url="url" :attribution="attribution" />
          <!-- 地標 -->
          <l-marker
            ref="location"
            :lat-lng="item.local"
            v-for="(item, index) in MapData"
            :key="item.name"
          >
            <l-icon :icon-url="iconColor(index)" />
            <!-- 地標圖示 -->
            <l-tooltip :options="tooltipOptions">
              <div class="tooltip-content">
                <div>{{ item.name }}</div>
              </div>
            </l-tooltip>
            <!-- 點擊圖標會彈出顯示的文字 -->
            <l-popup style="text-align: center">
              <div>{{ item.name }}</div>
              <div>目前有效容量比 : {{ damData[index].percent }}%</div>
            </l-popup>
          </l-marker>
        </l-map>
      </el-col>
      <!-- 阿公店水庫 -->
      <el-col :xs="24" :span="12" v-if="getDataFinished">
        <div id="w1" style="margin-bottom: 5%">{{ damData[0].name }}</div>
        <el-row style="text-align: center">
          <el-col :span="12">
            <div><span class="bigWord_before">原本</span>水庫資訊</div>
            <div
              id="originalpic"
              @mouseover="animation('in', 'originalpic')"
              @mouseout="animation('out', 'originalpic')"
            >
              {{ damData[0].capacitym3 }}m
              <sup>3</sup>
            </div>
            <div>水位（m）：{{ damData[0].capacitym }}m</div>
            <div>
              水庫容量（萬立方公尺）：{{ damData[0].capacitym3 }}m
              <sup>3</sup>
            </div>
          </el-col>
          <el-col :span="12">
            <div><span class="bigWord_after">目前</span>水庫資訊</div>
            <canvas
              id="sineCanvas"
              @mouseover="animation('in', 'sineCanvas')"
              @mouseout="animation('out', 'sineCanvas')"
            ></canvas>
            <div id="percent1">有效容量比：：{{ damData[0].percent }} %</div>
            <div>水位（m）：{{ damData[0].nowm }}m</div>
            <div>
              水庫容量（萬立方公尺）：{{ damData[0].nowm3 }}m
              <sup>3</sup>
            </div>
            <div>更新時間：{{ damData[0].time }}</div>
          </el-col>
        </el-row>
      </el-col>
    </el-row>
    <el-row>
      <!-- 澄清湖水庫 -->
      <el-col :span="12" :xs="24" v-if="getDataFinished">
        <div id="w2" style="margin-bottom: 5%">{{ damData[1].name }}</div>
        <el-row style="text-align: center">
          <el-col :span="12">
            <div><span class="bigWord_before">原本</span>水庫資訊</div>
            <div
              id="originalpic2"
              @mouseover="animation('in', 'originalpic2')"
              @mouseout="animation('out', 'originalpic2')"
            >
              {{ damData[1].capacitym3 }}m
              <sup>3</sup>
            </div>
            <div>水位（m）：{{ damData[1].capacitym }}m</div>
            <div>
              水庫容量（萬立方公尺）：{{ damData[1].capacitym3 }}m
              <sup>3</sup>
            </div>
          </el-col>
          <el-col :span="12">
            <div><span class="bigWord_after">目前</span>水庫資訊</div>
            <canvas
              id="sineCanvas2"
              @mouseover="animation('in', 'sineCanvas2')"
              @mouseout="animation('out', 'sineCanvas2')"
            ></canvas>
            <div id="percent2">有效容量比 : {{ damData[1].percent }} %</div>
            <div>水位 (m) : {{ damData[1].nowm }}m</div>
            <div>
              水庫容量（萬立方公尺（{{ damData[1].nowm3 }}m
              <sup>3</sup>
            </div>
            <div>更新時間：{{ damData[1].time }}</div>
          </el-col>
        </el-row>
      </el-col>
      <!-- 鳳山水庫 -->
      <el-col :span="12" :xs="24" v-if="getDataFinished">
        <div id="w3" style="margin-bottom: 5%">{{ damData[2].name }}</div>
        <el-row style="text-align: center">
          <el-col :span="12">
            <div><span class="bigWord_before">原本</span>水庫資訊</div>
            <div
              id="originalpic3"
              @mouseover="animation('in', 'originalpic3')"
              @mouseout="animation('out', 'originalpic3')"
            >
              {{ damData[2].capacitym3 }}m
              <sup>3</sup>
            </div>
            <div>水位（m）：{{ damData[2].capacitym }}m</div>
            <div>
              水庫容量（萬立方公尺）：{{ damData[2].capacitym3 }}m
              <sup>3</sup>
            </div>
          </el-col>
          <el-col :span="12">
            <div><span class="bigWord_after">目前</span>水庫資訊</div>
            <canvas
              id="sineCanvas3"
              @mouseover="animation('in', 'sineCanvas3')"
              @mouseout="animation('out', 'sineCanvas3')"
            ></canvas>
            <div id="percent3">有效容量比 : {{ damData[2].percent }} %</div>
            <div>水位（m）：{{ damData[2].nowm }}m</div>
            <div>
              水庫容量（萬立方公尺）：{{ damData[2].nowm3 }}m
              <sup>3</sup>
            </div>
            <div>更新時間：{{ damData[2].time }}</div>
          </el-col>
        </el-row>
      </el-col>
    </el-row>
    <!-- 卡爾森指數相關介紹 -->
    <hr class="hr" />
    <div class="childtitle two">你知道嗎？</div>
    <div class="content">
      <el-row :gutter="50" class="row_gutter">
        <!-- 介紹 -->
        <el-col :xs="24" :span="12">
          <div>
            用於評估水庫水質優養程度的指標為「<span class="explain_heilight2">
              卡爾森指數(Carlson trophic state index)</span
            >」，簡稱<span class="explain_heilight2">CTSI</span>， CTSI係以<span
              class="explain_heilight2"
              >水中的透明度（SD）、葉綠素a（Chl-a）</span
            >及
            <span class="explain_heilight2">總磷（TP）</span
            >等三項水質參數之濃度值進行計算，
            再以其計算所得之指標值，判定水庫水質之優養程度。
          </div>

          <el-table
            :data="CTSI_tableData"
            :header-cell-style="{
              background: '#1C629C',
              color: 'white',
              fontSize: '20px',
            }"
            :row-style="tableHeaderColor"
            style="margin: 30px 0"
          >
            <el-table-column prop="CTSI" label="CTSI指標值"> </el-table-column>
            <el-table-column prop="degree" label="水庫優養程度">
              <template slot-scope="scope">
                <el-tooltip placement="right">
                  <div slot="content" v-html="CTSIExplain[scope.$index]"></div>
                  <div>{{ scope.row.degree }}</div>
                </el-tooltip>
              </template>
            </el-table-column>
          </el-table>
          <div style="text-align: right; margin-top: 15px">
            ※ 更多詳細卡爾森指數算法情請洽：<br />
            <a href="https://wq.epa.gov.tw/Code/Business/Standard.aspx"
              >卡爾森指數詳細說明</a
            >
          </div>
        </el-col>
        <!-- 圖表 -->
        <el-col :xs="24" :span="12">
          <chart
            :options="ChartOption"
            v-loading="loading2"
            element-loading-text="等待政府資料中..."
            height="350"
            style="width: 100%; height: 300px"
          ></chart>
        </el-col>
      </el-row>
    </div>
    <!-- 各水庫監測站進階歷史紀錄查詢 -->
    <hr class="hr" />
    <div class="childtitle three">各水庫監測站進階歷史紀錄查詢</div>
    <div class="content">
      <div style="margin-bottom: 20px">
        <b>Tip：</b>
        點擊以下水庫，系統將會顯示該監測該水庫的所有測站位置，以及每個測站的卡爾森指數歷年變化。
      </div>
      <el-tabs
        class="searchMainRiver"
        tab-position="top"
        type="border-card"
        v-model="searchData.dam"
        @tab-click="search"
      >
        <el-tab-pane
          v-for="item in searchDamName"
          :key="item.name"
          :label="item.name"
          :disabled="!chartFinish"
          :name="item.englishName"
        >
        </el-tab-pane>
      </el-tabs>
      <el-card class="box-card">
        <el-row :gutter="20" v-if="StationMapData.length && width >= 450">
          <el-col :span="10">
            <div class="detailSearchTitle">測站位置</div>
          </el-col>
          <el-col :span="14">
            <div class="detailSearchTitle">卡爾森指數變化</div>
          </el-col>
        </el-row>
        <el-row :gutter="20" class="row_gutter">
          <!-- 地圖(測站打點) -->
          <el-col :xs="24" :span="10" v-if="StationMapData.length">
            <div
              class="detailSearchTitle"
              v-if="StationMapData.length && width < 450"
            >
              測站位置
            </div>
            <div>
              <l-map
                class="map"
                :zoom="stationZoom"
                :options="options"
                :center="stationCenter"
              >
                <!-- 載入地圖資料 -->
                <l-tile-layer :url="url" :attribution="attribution" />
                <!-- 地標 -->
                <l-marker
                  ref="location"
                  :lat-lng="item.local"
                  v-for="item in StationMapData"
                  :key="item.name"
                >
                  <l-icon :icon-url="iconimg[2]" />
                  <!-- 地標圖示 -->
                  <l-tooltip :options="tooltipOptions"
                    >{{ item.name }}
                  </l-tooltip>
                  <!-- 點擊圖標會彈出顯示的文字 -->
                  <l-popup style="text-align: center">
                    <div>{{ item.address }}</div>
                  </l-popup>
                </l-marker>
              </l-map>
            </div>
          </el-col>
          <!-- 各測站卡爾森指數歷史資料圖表 -->
          <el-col
            :xs="24"
            :span="14"
            v-loading="loading"
            element-loading-text="等待政府資料中..."
          >
            <div
              class="detailSearchTitle"
              v-if="StationMapData.length && width < 450"
            >
              卡爾森指數變化
            </div>
            <div v-for="(item, index) in searchtData[0]" :key="item.name">
              <div>{{ item.name }}</div>
              <chart
                v-if="chartFinish"
                :options="searchChartOption[index].option"
                height="150"
                style="width: 100%; height: 150px"
              ></chart>
            </div>
          </el-col>
        </el-row>
      </el-card>
    </div>
  </div>
</template>
<script>
import { mapState } from "vuex";
export default {
  async mounted() {
    this.width = window.innerWidth;
    this.water();
    await this.getStationName();
    await this.getCTSIdata();
    this.search();
  },
  computed: {
    ...mapState({
      nowChooseIndex: (state) => state.nowChooseIndex,
    }),
  },
  data() {
    return {
      width: 0,
      // ------------------------------------------------
      // |                水庫最新水量資訊               |
      // ------------------------------------------------
      // 水庫資料
      damData: [],
      // 水庫地圖圖標說明
      iconExplain: [
        "目前有效容量比 < 30%",
        "30% < 目前有效容量比 < 80%",
        "目前有效容量比 > 80%",
      ],
      //tooltip彈框設定
      tooltipOptions: {
        permanent: true,
        offset: [0, 10],
      },
      // ------ 地圖資料 ------
      zoom: 9, //水庫地圖縮放比例
      center: [22.712786, 120.342959], //水庫地圖中心
      stationZoom: 14, //水庫測站地圖縮放比例
      stationCenter: [22.712786, 120.342959], //水庫測站地圖中心
      // 地標圖示
      iconimg: [
        "https://cdn.rawgit.com/pointhi/leaflet-color-markers/master/img/marker-icon-2x-red.png",
        "https://cdn.rawgit.com/pointhi/leaflet-color-markers/master/img/marker-icon-2x-yellow.png",
        "https://cdn.rawgit.com/pointhi/leaflet-color-markers/master/img/marker-icon-2x-blue.png",
      ],
      url: "https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png", //地圖使用網址
      attribution: `© <a href="http://osm.org/copyright">OpenStreetMap</a> contributors`, //地圖來源聲明
      // 取消顯示地圖左上角縮放地圖的按鈕
      options: {
        zoomControl: false,
      },
      // 水庫地圖打點資料
      MapData: [
        {
          name: "阿公店水庫",
          local: [22.812786, 120.342959],
        },
        {
          name: "澄清湖水庫",
          local: [22.660768, 120.353766],
        },
        {
          name: "鳳山水庫",
          local: [22.535315, 120.384482],
        },
      ],
      // 一開始取得回來的所有測站資料
      StationMapData_original: {
        Agon: [],
        Chengcing: [],
        Fengshan: [],
      },
      StationMapData: [], // 測站地圖真正打點資料
      // ------------------------------------------------
      // |                 卡爾森指數介紹                |
      // ------------------------------------------------
      // 卡爾森指數優養化介紹
      CTSIExplain: [
        "大多在低度開發的狀態下<br/>水庫污染少、水體中營養鹽濃度較低、浮游動植物含量少<br/>此時<b> ' 水質狀況相對較佳 ' <b/>",
        "又稱為 ' 普養 ' <br/>為貧養階段到優養階段的過渡期<br/>營養鹽漸高、浮游動植物含量漸增",
        "藻類大量繁殖造成底層缺氧<br/>水中殘留物質增加、水體透明度顯著降低<br/>可能造成魚類等水中生物無法生存",
      ],
      // 卡爾森指數說明表格
      CTSI_tableData: [
        { CTSI: "CTSI < 40", degree: "貧養狀態" },
        { CTSI: "40 ≦ CTSI ≦ 50", degree: "普養狀態" },
        { CTSI: "CTSI > 50", degree: "優養狀態" },
      ],
      // 卡爾森指數圖表
      ChartOption: {
        title: {
          text: "100-108年高雄水庫卡爾森指數紀錄", //主標題
          textStyle: {
            color: "#006030",
            fontSize: 16,
            align: "center",
          },
          itemGap: 7,
        },
        tooltip: {
          trigger: "axis",
          axisPointer: {
            type: "shadow",
          },
        },
        legend: {
          data: ["澄清湖水庫", "鳳山水庫", "阿公店水庫"],
          right: 0,
          top: "8%",
        },
        xAxis: {
          type: "category",
          data: [],
        },
        yAxis: {
          type: "value",
        },
        series: [],
      },
      // ------------------------------------------------
      // |           水庫監測站進階歷史紀錄查詢           |
      // ------------------------------------------------
      // 進階搜尋請求回來的資訊
      searchtData: [],
      // 進階搜尋的綁定植
      searchData: {
        dam: "Agon",
        year: "",
      },
      // 進階搜尋的水庫tab
      searchDamName: [
        {
          name: "阿公店水庫",
          englishName: "Agon",
        },
        {
          name: "澄清湖水庫",
          englishName: "Chengcing",
        },
        {
          name: "鳳山水庫",
          englishName: "Fengshan",
        },
      ],
      // 進階搜尋圖表
      searchChartOption: [
        {
          option: {
            tooltip: {
              trigger: "axis",
              axisPointer: {
                type: "shadow",
              },
            },
            grid: {
              x: 30,
              y: 20,
              x2: 25,
              y2: 20,
            },
            xAxis: {
              type: "category",
              data: [],
            },
            yAxis: {
              type: "value",
            },
            series: [
              {
                data: [],
                name: "卡爾森指數",
                type: "line",
              },
            ],
          },
        },
        { option: {} },
        { option: {} },
        { option: {} },
      ],
      // ------------------------------------------------
      // |                  Boolean判斷                 |
      // ------------------------------------------------
      getDataFinished: false, // 是否已取得水庫即時資訊
      firstLoad: true, // 是否為第一次載入，若是第一次載入 => 記算阿公店水庫的卡爾森平均
      chartFinish: false, // 進階搜尋的圖表設置是否完成，若未完成 => 讓上方tab不能點，不然會報錯
      loading: false, // 進階搜尋的loading
      loading2: false, // 卡爾森紀錄圖表的loading
    };
  },
  methods: {
    // 取得水庫資料
    async water() {
      this.damData = require("@/file/water.json");
      this.getDataFinished = true;
      // 改變各水庫有效容量比顏色
      window.setTimeout(() => {
        var percent1 = document.getElementById("percent1");
        var percent2 = document.getElementById("percent2");
        var percent3 = document.getElementById("percent3");
        if (this.damData[0].percent < 30) {
          percent1.style.color = "red";
        } else if (
          this.damData[0].percent > 30 &&
          this.damData[0].percent < 80
        ) {
          percent1.style.color = "#DAA520";
        } else {
          percent1.style.color = "green";
        }
        if (this.damData[1].percent < 30) {
          percent2.style.color = "red";
        } else if (
          this.damData[1].percent > 30 &&
          this.damData[1].percent < 80
        ) {
          percent2.style.color = "#DAA520";
        } else {
          percent2.style.color = "green";
        }
        if (this.damData[2].percent < 30) {
          percent3.style.color = "red";
        } else if (
          this.damData[2].percent > 30 &&
          this.damData[2].percent < 80
        ) {
          percent3.style.color = "#DAA520";
        } else {
          percent3.style.color = "green";
        }

        this.drawWater("1", this.damData[0].percent);
        this.drawWater("2", this.damData[1].percent);
        this.drawWater("3", this.damData[2].percent);
      }, 0);
    },
    // 取得測站位置資料
    async getStationName() {
      let data = require("@/file/waterpool_station.json");
      data[0].forEach((item) => {
        let info = {
          name: "",
          address: "",
          local: [22.812786, 120.342959],
        };
        info.name = item.SiteName;
        info.address = item.SiteAddress;
        info.local[0] = item.TWD97Lat;
        info.local[1] = item.TWD97Lon;
        switch (item.DamName) {
          case "阿公店水庫":
            this.StationMapData_original["Agon"].push(info);
            break;
          case "澄清湖水庫":
            this.StationMapData_original["Chengcing"].push(info);
            break;
          case "鳳山水庫":
            this.StationMapData_original["Fengshan"].push(info);
            break;
        }
      });
      console.log(this.StationMapData_original);
    },
    // 進階查詢
    search() {
      this.loading = true;
      this.chartFinish = false;
      this.searchtData = [];
      let chineseDamName = "";
      switch (this.searchData.dam) {
        case "Agon":
          chineseDamName = "阿公店水庫";
          break;
        case "Chengcing":
          chineseDamName = "澄清湖水庫";
          break;
        case "Fengshan":
          chineseDamName = "鳳山水庫";
          break;
      }
      // 將選擇的流域所有測站打點在地圖上
      this.StationMapData = this.StationMapData_original[this.searchData.dam];
      console.log(this.StationMapData);
      this.stationCenter = this.StationMapData[0].local;
      let data = require("@/file/waterpool_detailSearch.json");
      data.forEach((item) => {
        if (item[1].name.indexOf(chineseDamName) !== -1) {
          this.searchtData.push(JSON.parse(JSON.stringify(item)));
        }
      });
      console.log(this.searchtData);
      this.searchChartSetting();
      this.chartFinish = true;
      this.loading = false;
    },
    // 進階搜尋圖表資料設定
    searchChartSetting() {
      let copyData = JSON.parse(JSON.stringify(this.searchtData));
      for (let i = 0; i < this.searchtData[0].length - 1; i++) {
        // 設定每個圖表的基本設定 ( 複製第一個的格式 )
        this.searchChartOption[i].option = JSON.parse(
          JSON.stringify(this.searchChartOption[0].option)
        );
        this.searchChartOption[i].option.xAxis.data = [];
        this.searchChartOption[i].option.series[0].data = [];
        this.searchtData.forEach((item) => {
          this.searchChartOption[i].option.xAxis.data.push(item[0]);
        });
      }
      this.searchtData.forEach((item) => {
        for (let i = 1; i < item.length; i++) {
          this.searchChartOption[i - 1].option.series[0].data.push(
            item[i].value
          );
        }
        item.splice(0, 1); // 刪除年份資料
      });
      console.log(this.searchChartOption);
      // 第一次載入 => 處理阿公店水庫的卡爾森指數平均
      if (this.firstLoad) {
        this.firstLoad = false;
        copyData = copyData.filter((item) => {
          let timeText = item[0] - 1911 + "年";
          return this.ChartOption.xAxis.data.indexOf(timeText) !== -1;
        });
        let avg = [];
        copyData.forEach((item) => {
          let sum = 0;
          for (let i = 1; i < 4; i++) {
            sum += parseFloat(item[i].value);
          }
          avg.push((sum / 3).toFixed(2));
        });
        let data1 = {
          name: "阿公店水庫",
          data: [],
          type: "line",
        };
        avg.forEach((item) => {
          data1.data.push(item);
        });
        this.ChartOption.series.push(data1);
        console.log(this.ChartOption.series);
        this.loading2 = false;
      }
    },
    // 取得卡爾森指數資料
    async getCTSIdata() {
      let chartXdata = this.ChartOption.xAxis.data;
      this.loading2 = true;
      let damName = ["澄清湖水庫", "鳳山水庫"];
      let CTSIdata = require("@/file/waterpool_CTSIdata.json");
      CTSIdata.forEach((item, index) => {
        var data1 = {
          name: damName[index],
          data: [],
          type: "line",
        };
        item.forEach((detail) => {
          data1.data.push(detail.value1);
          if (index === 0) {
            chartXdata.push(detail.item1);
          }
        });
        this.ChartOption.series.push(data1);
      });
      this.ChartOption.title.text = `${chartXdata[0]}-${
        chartXdata[CTSIdata[0].length - 1]
      }高雄水庫卡爾森指數紀錄`;
    },
    // 水庫汙染指數介紹table的row顏色設定
    tableHeaderColor({ row }) {
      if (row.degree === "貧養狀態") {
        return {
          "background-color": "#FFFACD",
        };
      }
      if (row.degree === "普養狀態") {
        return {
          "background-color": "#E2F0D9",
        };
      }
      if (row.degree === "優養狀態") {
        return {
          "background-color": "#FFE4E1",
        };
      }
    },
    // 根據水庫的目前有效容量比，更改地圖上icon顏色
    iconColor(index) {
      if (this.damData[index].percent < 30) {
        return this.iconimg[0];
      } else if (
        this.damData[index].percent > 30 &&
        this.damData[index].percent < 80
      ) {
        return this.iconimg[1];
      } else {
        return this.iconimg[2];
      }
    },
    // 各canvas滑鼠移入移出opacity改變效果
    animation(status, element) {
      switch (element) {
        case "sineCanvas":
          var item = document.getElementById("sineCanvas");
          if (status === "in") {
            item.style.opacity = 1;
          } else {
            item.style.opacity = 0.5;
          }
          break;
        case "sineCanvas2":
          var item2 = document.getElementById("sineCanvas2");
          if (status === "in") {
            item2.style.opacity = 1;
          } else {
            item2.style.opacity = 0.5;
          }
          break;
        case "sineCanvas3":
          var item3 = document.getElementById("sineCanvas3");
          if (status === "in") {
            item3.style.opacity = 1;
          } else {
            item3.style.opacity = 0.5;
          }
          break;
        case "originalpic":
          var item4 = document.getElementById("originalpic");
          if (status === "in") {
            item4.style.opacity = 1;
          } else {
            item4.style.opacity = 0.5;
          }
          break;
        case "originalpic2":
          var item5 = document.getElementById("originalpic2");
          if (status === "in") {
            item5.style.opacity = 1;
          } else {
            item5.style.opacity = 0.5;
          }
          break;
        case "originalpic3":
          var item6 = document.getElementById("originalpic3");
          if (status === "in") {
            item6.style.opacity = 1;
          } else {
            item6.style.opacity = 0.5;
          }
          break;
      }
    },
    // 水填滿canvas
    drawWater(name, percent) {
      var unit = 100,
        canvas,
        context,
        height,
        width,
        xAxis,
        yAxis;
      var percentMax = percent / 100; //液體占容器的百分比
      var percentMin = 0;
      var maxHeight = 1;
      if (percentMax >= 1) {
        maxHeight = 0.95;
      }
      var wavePower = 3;
      //初始化
      function init() {
        window.requestAnimFrame = (function () {
          return (
            window.requestAnimationFrame ||
            window.webkitRequestAnimationFrame ||
            window.mozRequestAnimationFrame ||
            function (callback) {
              window.setTimeout(callback, 1000 / 60);
            }
          );
        })();
        if (name == "1") {
          canvas = document.getElementById("sineCanvas");
        } else if (name == "2") {
          canvas = document.getElementById("sineCanvas2");
        } else {
          canvas = document.getElementById("sineCanvas3");
        }
        canvas.width = 1000;
        canvas.height = 1000;
        context = canvas.getContext("2d");
        height = canvas.height;
        width = canvas.width;
        yAxis = 0;
        draw();
      }
      function draw() {
        // 清理canvas畫板
        context.clearRect(0, 0, width, height);
        //描繪波浪
        if (percent > 80) {
          drawWave(["#77DDFF", "#0044BB"], 1, 1.5);
        } else if (percent > 30 && percent < 80) {
          drawWave(["#F0E68C", "#B8860B"], 1, 1.5);
        } else {
          drawWave(["#FFA07A", "#FF4500"], 1, 1.5);
        }
        if (percentMin <= percentMax) {
          percentMin = percentMin + 0.002;
          xAxis = Math.floor(height * (maxHeight - percentMin));
        }
        if (percentMin > 0.8) {
          if (wavePower <= 36 * percentMax) {
            wavePower = wavePower + 0.05;
          }
        }
        // 更新時間然後重新繪制
        draw.seconds = draw.seconds + 0.009;
        draw.t = draw.seconds * Math.PI;
        window.requestAnimFrame(draw);
      }
      draw.seconds = 0;
      draw.t = 0;
      /**
       * 描繪波浪
       * drawWave([漸變左側颜色，右側颜色], 不透明度, 波浪的幅度)
       */
      function drawWave(color, alpha, zoom) {
        context.globalAlpha = alpha;
        var linearGrad = context.createLinearGradient(0, 0, width, 0);
        linearGrad.addColorStop(0.0, color[0]);
        linearGrad.addColorStop(1.0, color[1]);
        context.fillStyle = linearGrad;

        context.beginPath(); //開始路徑
        drawSine(draw.t / 0.5, zoom);
        context.lineTo(width + 10, height); //路徑朝canvas右下
        context.lineTo(0, height); //路徑朝canvas左下
        context.closePath(); //封閉路徑
        context.fill(); //塗色
      }
      /**
       * 畫正弦
       * drawSine(時間, 波浪的浮動)
       */
      function drawSine(t, zoom) {
        var x = t; //將時間轉換為x軸
        var y = Math.sin(x) / zoom;
        context.moveTo(yAxis, unit * y + xAxis); //在開始位置放置路径
        // 繪制波浪、横向幅度
        for (var i = yAxis; i <= width + 10; i += 10) {
          x = t + (-yAxis + i) / unit / zoom;
          y = Math.sin(x - 0) / wavePower;
          context.lineTo(i, unit * y + xAxis);
        }
      }
      init();
    },
  },
};
</script>
